.profile_container{
    width: 500px;
    height: 100vh;
    margin: auto;
    margin-top: 50px;

    display: flex;
    flex-direction: column;
    align-items: center;
}

.logout_button{
    width: 150px;
    height: 30px;
    color: aliceblue;
    background-color: black;
    border-radius: 20px;
}
.profile_container h1{
    margin-bottom: 30px;
}

.inpt{
    margin-bottom: 10px;
}
.inpt input{
    width: 250px;
    height: 30px;
}

.profile_inner_container{

    width: 400px;
    height: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 30px;
}
.profile_inner_container form{
    display: flex;
    flex-direction: column;
}
.profile_inner_container button {
    height: 30px;
    color: aliceblue;
    background-color: black;
    border-radius: 20px;
}

.inpt{
    align-self: center;
}

.search_container{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 100px;
}


.s_container{
    display: flex;
    width: 60%;
    height: 50px;
    justify-content: space-around;

}

.s_container2{

    margin-left: 0px;
    width: 150px;
display: flex;
justify-content: start;
}

.send_button:hover{
  background-color: rgb(43, 45, 47);
}

.send_button{
    border-radius: 30px;
    background-color: black;
    color: white;
    width: 100px;
    margin-right: 2px;
}

img{
    width: 60px;
}

.r_container{
    margin-top: 20px;
  
    display: flex;
    flex-direction: column;
    align-items: center;
}

.r_container2{
    margin-top: 20px;
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.r_container3{
    border: 1px solid black;
    display:flex;
    flex-direction: row;
    justify-content: space-around;
   
    width: 350px;
}

.r_container3 p{
    font-size: 25px;
    margin-left: -40px;
}

.pr_container{
    border: 1px solid black;
    margin: auto;
    margin-top: 60px;
    height: 70vh;
    width: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

