.e_container{
    height: 70vh;
    margin-top: 70px;
    display: flex;
    justify-content: center;
}

.e_container form{
    border:1px solid black;
    width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 60px;
    margin-bottom: 60px;
}

.e_container label{
    font-size: 20px;
    margin-bottom: 5px;
    margin-left: 20px;
}

.e_container input{
    margin-bottom: 8px;
    height: 25px;
    width: 250px;
    margin-left: 20px;
}

.e_container button{
    height: 25px;
    width: 250px;
    background-color: black;
    color: aliceblue;
    margin-left: 20px;
    border-radius: 20px;
}

.p_container{
    width: 400px;
    height: 80vh;
    margin: auto;
    margin-top: 50px;
    border:1px solid black;
    display: flex;
    flex-direction:column;
    justify-content: center;
}
.p_container input{
    height: 25px;
    width: 250px;
    margin-left: 57px;
    margin-bottom: 10px;
}
.p_container button{
    height: 25px;
    width: 250px;
    margin-left: 60px;
    border-radius: 20px;
    background-color: black;
    color: aliceblue;
}

.p_container h1{
    margin-left: 60px;
}

