section{
 
    width: 100%;
    min-height:100vh;

    border: 1px solid black;

    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    
}

.sign_form{
    border: 1px solid #c9c9c9;
    padding: 10px 30px;
    border-radius: 5px;
    margin-top: 40px;
    margin-bottom: 70px;
}

.sign_form h1{
    font-weight: 500;
    font-size: 27px;
    margin: 10px 0;
}

.sign_form label{
    font-size: 13px;
    font-weight: 700;
    margin-bottom: 1px;
}

.sign_form input{
    width: 97%;
    height: 27px;
    /* padding: 3px 7px; */
    border-radius: 3px;
    outline: none;
    border:1px solid #a6a6a6;
    margin-bottom: 10px;
    margin-top: 1px;
}

.sign_form .signin_btn{
    margin-top: 7px;
    margin-bottom: 23px;
    margin-left: 45px;
    width: 50%;
    cursor: pointer;
    height: 29px;
    border-radius: 15px;
    color: #f2efef;
    background-color: #0d0d0c;
}

.signin_info {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;

}


.create_accountinfo{
    margin-left: 12%;
}
.create_accountinfo2{
    margin-left: 12%;
    margin-bottom: 10px;
    margin-left: 38px;
}
